/* CSS */
button.button-4,
button.button-4:before,
button.button-4:after {
    box-sizing: border-box;
}

button.button-4 {
    touch-action: manipulation;
    position: relative;
    background: transparent;
    border: none;
    outline: none;
    display: block;
    height: 60px;
    width: 175px;
    padding: 20px;
    font-size: 20px;
    line-height: 20px;
    font-weight: 100;
    text-transform: uppercase;
    margin: 0 auto;
    border-radius: 10px;
    top: 0;
    -webkit-transition: 0.15s ease-in-out;
    -moz-transition: 0.15s ease-in-out;
    -ms-transition: 0.15s ease-in-out;
    -o-transition: 0.15s ease-in-out;
    transition: 0.15s ease-in-out;
}

button.button-4 {
    touch-action: manipulation;
    -webkit-transform: perspective(500px) rotateX(35deg);
}

button.button-4:before {
    content: "";
    display: block;
    position: absolute;
    z-index: 1;
    background: #f25d30;
    /* Old browsers */
    background: -moz-linear-gradient(top, #f25d30 0%, #e03b00 100%);
    /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #f25d30), color-stop(100%, #e03b00));
    /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top, #f25d30 0%, #e03b00 100%);
    /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(top, #f25d30 0%, #e03b00 100%);
    /* Opera 11.10+ */
    background: -ms-linear-gradient(top, #f25d30 0%, #e03b00 100%);
    /* IE10+ */
    background: linear-gradient(to bottom, #f25d30 0%, #e03b00 100%);
    /* W3C */
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#f25d30", endColorstr="#e03b00", GradientType=0);
    /* IE6-9 */
    top: 0;
    left: 0;
    border-radius: 5px;
    width: 100%;
    height: 100%;
    padding: 20px;
    box-shadow: inset 0 -2px 5px rgba(255, 255, 255, 0.3);
}

button.button-4:after {
    content: "";
    display: block;
    position: absolute;
    bottom: -10px;
    left: 1px;
    height: 15px;
    width: 173px;
    background: #ba2500;
    /* Old browsers */
    background: -moz-linear-gradient(top, #ba2500 0%, #9b3100 100%);
    /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #ba2500), color-stop(100%, #9b3100));
    /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top, #ba2500 0%, #9b3100 100%);
    /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(top, #ba2500 0%, #9b3100 100%);
    /* Opera 11.10+ */
    background: -ms-linear-gradient(top, #ba2500 0%, #9b3100 100%);
    /* IE10+ */
    background: linear-gradient(to bottom, #ba2500 0%, #9b3100 100%);
    /* W3C */
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ba2500", endColorstr="#9b3100", GradientType=0);
    /* IE6-9 */
    border-radius: 0 0 5px 5px;
    -webkit-transform: perspective(500px) rotateX(-40deg);
    box-shadow: inset 0 -2px 3px rgba(0, 0, 0, 0.5), 0 3px 2px -1px black;
    -webkit-transition: 0.15s ease-in-out;
    -moz-transition: 0.15s ease-in-out;
    -ms-transition: 0.15s ease-in-out;
    -o-transition: 0.15s ease-in-out;
    transition: 0.15s ease-in-out;
}

button.button-4:hover {
    top: -3px;
}

button.button-4:hover:after {
    height: 18.5px;
    bottom: -13px;
}

button.button-4:active {
    top: 0px;
}

button.button-4:active:after {
    height: 15px;
    bottom: -10px;
}

button.button-4 .text {
    position: relative;
    z-index: 2;
    font-weight: bold;
    color: white;
    text-shadow: 0 2px 2px rgba(0, 0, 0, 0.4);
}




/* CSS */
.button-5 {
    appearance: button;
    background-color: #1899D6;
    border: solid transparent;
    border-radius: 16px;
    border-width: 0 0 4px;
    box-sizing: border-box;
    color: #FFFFFF;
    cursor: pointer;
    display: inline-block;
    font-family: din-round, sans-serif;
    font-size: 15px;
    font-weight: 700;
    letter-spacing: .8px;
    line-height: 20px;
    margin: 0;
    outline: none;
    overflow: visible;
    padding: 13px 16px;
    text-align: center;
    text-transform: uppercase;
    touch-action: manipulation;
    transform: translateZ(0);
    transition: filter .2s;
    user-select: none;
    -webkit-user-select: none;
    vertical-align: middle;
    white-space: nowrap;
}

.button-5:after {
    background-clip: padding-box;
    background-color: #1CB0F6;
    border: solid transparent;
    border-radius: 16px;
    border-width: 0 0 4px;
    bottom: -4px;
    content: "";
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;
}

.button-5:main,
.button-5:focus {
    user-select: auto;
}

.button-5:hover:not(:disabled) {
    filter: brightness(1.1);
    -webkit-filter: brightness(1.1);
}

.button-5:disabled {
    cursor: auto;
}

.button-5:active {
    border-width: 4px 0 0;
    background: none;
}


/* CSS */
.button-6 {
    touch-action: manipulation;
    background-image: linear-gradient(#4098ff, #4058ff 62%, #4075ff);
    border: .5px solid #3247cf;
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, .12) 0 1px 3px, rgba(0, 0, 0, .14) 0 1px .5px, rgba(182, 229, 255, .4) 0 1px .5px inset, rgba(23, 45, 202, .46) 0 -1px .5px inset, #233ced 0 0 0 .5px inset;
    color: #fff;
    font-weight: 600;
    outline: 3px #fff;
    outline-offset: 0;
    position: relative;
    text-shadow: #233ced 0 -1px .5px;
    top: 0;
    display: inline-block;
    font-size: 20px;
    line-height: 100%;
    padding: 12px 24px 14px;

    /* optional */
    transition-property: outline-offset, border-color, transform;
    transition-duration: .2s;
}

.button-6:active,
.button-6:hover {
    outline: 0;
}

.button-6:hover {
    background-image: linear-gradient(#40a6ff, #4058ff 62%, #4075ff);
    box-shadow: rgba(0, 0, 0, .12) 0 2px 6px, rgba(0, 0, 0, .14) 0 1px .5px, rgba(182, 229, 255, .4) 0 1px .5px inset, rgba(23, 45, 202, .46) 0 -1px .5px inset, #233ced 0 0 0 .5px inset;
    opacity: 1;

    /* optional */
    transform: scale(1.04);
}

.button-6:active {
    background-image: linear-gradient(#0a40ce, #4058ff 38%, #40a0ff);
    border-color: #192ba1;
    box-shadow: #0b1c95 0 1px inset, transparent 0 -1px .5px inset, rgba(13, 33, 176, .53) 0 2px .5px inset, rgba(35, 60, 237, .31) 0 0 0 .5px inset;
    text-shadow: #233ced 0 1px .5px;

    /* optional */
    transform: scale(.98);
}


/* CSS */
.button-7 {
    touch-action: manipulation;
    cursor: pointer;
    position: relative;
    padding: 1.25rem 3.75rem;
    border-radius: 1.87rem;
    line-height: 1.25rem;
    font-size: 1rem;
    font-weight: 600;
    transition: all 150ms ease-in-out;

    border: 1px solid #012880;
    background-image: linear-gradient(-180deg, #FF89D6 0%, #C01F9E 100%);
    box-shadow: 0 0.5rem 0.625rem 0 rgba(0, 0, 0, 0.2),
        0 -0.125rem 0.75rem rgba(110, 15, 155, 1) inset,
        0 0.375rem 0.125rem rgba(255, 255, 255, 0.4) inset,
        0 0.125rem 0.25rem 0 rgba(180, 70, 207, 1) inset;
}

.button-7 span {
    color: transparent;
    background-image: linear-gradient(0deg, #EE82DA -10%, #FEFAFD 100%);
    -webkit-background-clip: text;
    background-clip: text;
    filter: drop-shadow(0 2px 2px hsla(290, 100%, 20%, 1));
}

.button-7::before {
    content: "";
    display: block;
    height: 0.125rem;
    position: absolute;
    top: 0.25rem;
    left: 50%;
    transform: translateX(-50%);
    width: calc(100% - 3.75rem);
    background: #fff;
    border-radius: 100%;

    opacity: 0.7;
    background-image: linear-gradient(-270deg, rgba(255, 255, 255, 0.00) 0%, #FFFFFF 20%, #FFFFFF 80%, rgba(255, 255, 255, 0.00) 100%);
}

.button-7::after {
    content: "";
    display: block;
    height: 0.125rem;
    position: absolute;
    bottom: 0.375rem;
    left: 50%;
    transform: translateX(-50%);
    width: calc(100% - 3.75rem);
    background: #fff;
    border-radius: 100%;

    filter: blur(1px);
    opacity: 0.05;
    background-image: linear-gradient(-270deg, rgba(255, 255, 255, 0.00) 0%, #FFFFFF 20%, #FFFFFF 80%, rgba(255, 255, 255, 0.00) 100%);
}

.button-7:hover {
    filter: brightness(1.1);
}

.button-7:active {
    transform: scale(.96);
}

/* CSS */
.button-8 {
    touch-action: manipulation;
    appearance: button;
    background-color: transparent;
    border-style: initial;
    border-width: 0;
    cursor: pointer;
    margin: 0;
    text-transform: none;
    vertical-align: baseline;
}

.button-8:focus {
    outline: 0;
}

.button-8 {
    touch-action: manipulation;
    background-image: linear-gradient(135deg, color(display-p3 .572549 .415686 1) 0%, color(display-p3 1 .466667 .690196) 50%, color(display-p3 1 .701961 .403922) 100%);
    border-radius: 8px;
    color: #fff;
    font-feature-settings: "ss01", "cv10", "calt" 0, "liga" 0;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    padding: 10px 20px;
    position: relative;
    transition: transform .2s cubic-bezier(.6, .6, 0, 1), margin-top .2s cubic-bezier(.6, .6, 0, 1);
    transition-behavior: normal, normal;
}

.button-8::before {
    -webkit-mask-clip: content-box, initial;
    -webkit-mask-composite: xor;
    -webkit-mask-image: linear-gradient(#fff 0, #fff 0), linear-gradient(#fff 0, #fff 0);
    -webkit-mask-origin: content-box, initial;
    -webkit-mask-position-x: initial, initial;
    -webkit-mask-position-y: initial, initial;
    -webkit-mask-repeat-x: initial, initial;
    -webkit-mask-repeat-y: initial, initial;
    -webkit-mask-size: initial, initial;
    padding: 1px;
}

.button-8::before {
    background-color: initial;
    background-image: linear-gradient(rgba(255, 255, 255, .32), transparent);
    bottom: 0;
    pointer-events: none;
    right: 0;
}

.button-8::after,
.button-8::before {
    border-radius: inherit;
    content: "";
    left: 0;
    position: absolute;
    top: 0;
}

.button-8::after {
    box-shadow: #000019 0 0, rgba(0, 0, 25, .3) 0 9px 21px, rgba(73, 2, 57, .2) 0 -3px inset;
    height: 100%;
    transition: box-shadow .2s cubic-bezier(.6, .6, 0, 1), background-color .2s cubic-bezier(.6, .6, 0, 1);
    transition-behavior: normal, normal;
    width: 100%;
    z-index: 1;
}

.button-8:active {
    transform: translateY(1.5px);
}

.button-8:hover::after {
    background-color: rgba(255, 255, 255, .1);
}

.button-8:active::after {
    box-shadow: #000019 0 0, rgba(0, 0, 25, .2) 0 9px 21px, rgba(73, 2, 57, .2) 0 0 inset;
}

