/* CSS */
.button-11 {
  touch-action: manipulation;
  position: relative;
  padding: 0;
  width: 100px;
  height: 100px;
  border: 2px solid #888888;
  outline: none;
  background-color: #f4f5f6;
  border-radius: 20px;
  box-shadow: -3px -10px 17.5px #ffffff, -3px -5px 7.5px #ffffff, -10px 0px 15px #ffffff, 3px 10px 12.5px rgba(0, 0, 0, 0.2);
  transition: 0.13s ease-in-out;
  cursor: pointer;
}

.button-11:active {
  box-shadow: none;
}

.button-11:active .button-11__content {
  box-shadow: none;
}

.button-11:active .button-11__content .button-11__text,
.button-11:active .button-11__content .button-11__icon {
  transform: translate3d(0px, 0px, 0px);
}

.button-11__content {
  position: relative;
  display: grid;
  padding: 10px;
  width: 100%;
  height: 100%;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  box-shadow: inset 0px -4px 0px #dddddd, 0px -4px 0px #f4f5f6;
  border-radius: 20px;
  transition: 0.13s ease-in-out;
  z-index: 1;
}

.button-11__icon {
  position: relative;
  display: flex;
  transform: translate3d(0px, -2px, 0px);
  grid-column: 4;
  align-self: start;
  justify-self: end;
  width: 16px;
  height: 16px;
  transition: 0.13s ease-in-out;
}

.button-11__icon svg {
  width: 16px;
  height: 16px;
  fill: #aaaaaa;
}

.button-11__text {
  position: relative;
  transform: translate3d(0px, -2px, 0px);
  margin: 0;
  align-self: end;
  grid-column: 1/5;
  grid-row: 2;
  text-align: center;
  font-size: 16px;
  background-color: #888888;
  color: transparent;
  text-shadow: 2px 2px 3px rgba(255, 255, 255, 0.5);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  transition: 0.13s ease-in-out;
}

/* CSS */
.button-12 {
  touch-action: manipulation;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  background-color: #000;
